@charset "utf-8";

.navigation{
  width:1200px;
  margin:0 auto;
}
.navigation ul{
  display: flex;
  justify-content: space-around;
  list-style: none;
}
.navigation ul a{
  color:#333;
  text-decoration: none;
  font-weight:600;
  padding:3px 20px;
}
.navigation ul a:hover{
  background:rgb(207, 40, 68);
  color:#fff;
}